#cont1 {
  width: 100%;
  /* height: 100px; */
  /* background-color: #b73030; */
  position: relative;
  display: flex;
  /* top: 4.5vw; */
  padding-top: 4.5vw;
}
#cont1_left {
  width: 50%;
  /* height: 250px; */
  /* background-color: antiquewhite; */
  margin-right: 0.8vw;
  overflow: hidden;
}
#model1 {
  width: 100%;
}
#cont1_right {
  width: 50%;
  /* height: 250px; */
  /* background-color: rgb(128, 88, 35); */
}
#coupon {
  width: 23%;
  height: 3vw;
  background: #b73030;
  position: absolute;
  padding: 0.8vw;
  z-index: 2;
  /* bottom: 0; */
}
#coupon p {
  font-size: 1.2vw;
  font-weight: 600;
  font-family: "Mohave", sans-serif;
  position: absolute;
}

.search {
  position: absolute;
  width: 15vw;
  height: 3.4vw;
  background-color: #393c3e;
  border: none;
  transform: translateX(18vw);
  color: #ffffff;
  padding-left: 1vw;
}
.search:hover {
  background-color: #0f2c66;
}
.searchbtn {
  width: 3.5vw;
  height: 3.4vw;
  background-color: #393c3e;
  position: absolute;
  transform: translateX(33vw);
  cursor: pointer;
  border: none;
  font-size: 1vw;
}
.wishlist {
  width: 6.5vw;
  height: 3.4vw;
  background-color: #ffffff;
  position: absolute;
  transform: translateX(36.5vw);
  color: #b73030;
  cursor: pointer;
  border: none;
  font-weight: 600;
  font-size: 0.9vw;
}
.wishlist:hover {
  background-color: rgb(158, 158, 158);
  color: #ffffff;
}
.cart {
  width: 6vw;
  height: 3.3vw;
  background-color: #b73030;
  transform: translateX(43vw);
  border: none;
  cursor: pointer;
  position: absolute;
  font-size: 1vw;
}
#cont1_center {
  width: 65vw;
  height: 15vw;
  background-color: #ffffff;
  bottom: 17vw;
  right: 2vw;
  /* transform: translateX(30vw); */
  margin: 0 15%;
  position: absolute;
  border-radius: 1.5vw;
  z-index: 5;
}
#urban {
  width: 9vw;
  height: 9vw;
  border-radius: 50vw;
  position: relative;
  top: -5vw;
  right: 2vw;
  transform: translateX(30vw);
}
#cont1_center h1 {
  line-height: 24px;
  vertical-align: baseline;
  letter-spacing: normal;
  word-spacing: 0px;
  line-height: 24px;
  vertical-align: baseline;
  letter-spacing: normal;
  word-spacing: 0px;
  text-align: center;
  font-size: 2.6vw;
  transform: translateY(-3.9vw);
  font-family: "Rufina", serif;
  color: #0f2c66;
}
#cont1_center h3 {
  transform: translateY(-3.3vw);
  font-size: 1.7vw;
  margin-left: 16vw;
  font-family: "Rufina", serif;
}
#cont1_center #shop {
  /* position: absolute; */
  padding: 1.2vw 4vw;
  margin: 0 25vw;
  font-size: 1.5vw;
  border-radius: 1vw;
  border: none;
  cursor: pointer;
  background-color: #b73030;
  color: #ffffff;
  font-family: "Mohave", sans-serif;
  font-weight: 500;
}
#cont1_center #shop:hover {
  background-color: #0f2c66;
}
.cart i {
    color: #ffffff;
    font-size: 1.4vw;
  }
  .searchbtn i {
    color: #ffffff;
  }
